import './App.css';
import "./Components/Colors/blue-white.css"
import "./Components/Colors/purples.css"
import React, { useEffect, useState } from 'react';
import {
  createBrowserRouter,
  RouterProvider,
  Routes
} from "react-router-dom";

import Lottie from 'react-lottie';


import Teste from "./Pages/PolicyPrivacy";
import Button from '@mui/material/Button';
import AllPaths from "./Routes/Routes";
import * as animation from "./Components/Loading/rollingBlue.json";
import Menu from './Routes/Menu';

// const Routes = createBrowserRouter(AllPaths.Main);


const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: animation,
  rendererSettings: {
    preserveAspectRatio: 'xMidYMid slice',
  },
};

const App = () => {
  const [pageTitle, setPageTitle] = useState('Company BEJ');
  const [Paths, setPaths] = useState(AllPaths.Main)
  const [loading, setLoading] = useState(true);
  const [shownMenu, setShownMenu] = useState(true);


  const router = createBrowserRouter(Paths);

  useEffect(() => {
    document.title = pageTitle;
    console.log("igual", router.state.matches[0].pathname, router.state.matches[0].route.path)
    console.log(router.state.location.pathname)
    console.log("name", Routes.name)
    if (router.state.location.pathname != "/"
      && router.state.location.pathname != "/privacy-policy"
      // && router.state.location.pathname == router.state.matches[0].pathname
    ) {
      setShownMenu(false)
    }
    window.onload = function () {
      console.log("The entire site has been loaded.");
      setLoading(false);
    }
    // setTimeout(() => {
    //   setLoading(false);
    // }, 2000); // adjust the timeout according to your needs

  }, [pageTitle]);

  const newTitle = (value) => {
    // console.log(value)
    setPageTitle(value);
  };


  return (
    <div className="App bgBlueWhite1">
      {shownMenu ?
        <Menu />
        : (null)
      }
      {loading ? (
        <Lottie options={defaultOptions} height={200} width={200} />
      ) : (
        <>
          {/* <Teste title={newTitle}></Teste> */}
          <RouterProvider router={router} />
        </>
      )}
    </div>
  );
};

export default App;