import "./css/Card.css";
import React from "react";
import { Link } from "react-router-dom";

export default (props) => {

    const cardStyle = {
        backgroundColor: props.color || '#044BD9',
        borderColor: props.color || '#044BD9',
    }
    const onPress = () => {
        // console.log("to",props.to)
        window.location.href = props.to
    }
    return (
        <div className="Card" style={cardStyle} onClick={onPress} >
            <div className="Title">{props.titulo}</div>
            <div className="Content" style={{backgroundColor: props.bgColor}}>
                {props.children}
            </div>
        </div>
    );
};
