import React, { useEffect, useState } from "react";
import { createBrowserRouter, useRouteError } from "react-router-dom";
import moment from "moment";
import AllPaths from "../Routes/Routes";

export default function ErrorPage() {
    const [Paths, setPaths] = useState(AllPaths.Main)
    const router = createBrowserRouter(Paths);


    const initialSeconds = 5;
    const [second, setSecond] = useState(5) // Set the target date and time (in the future)

    // Use the useState hook to create a state variable for the countdown
    // const [countdown, setCountdown] = useState(moment.duration(targetDate.diff(moment())/1000),"seconds");
    const [countdown, setCountdown] = useState(moment.duration(second, 'seconds').asSeconds().toString() + "s");

    const error = useRouteError();
    // console.error(error);
    useEffect(() => {
        document.title = "Error";
        console.log('r', document.referrer)
        console.log(router.routes)
        console.log(router.state.matches[0].pathname)
        setTimeout(() => {
            if (document.referrer == null || document.referrer == undefined || document.referrer == ""
            ) {
                window.location.href = '/';
            }
            else {
                window.location.href = document.referrer;
            }

        }, initialSeconds * 1000); // adjust the timeout according to your needs
    }, []);

    useEffect(() => {
        const timer = setInterval(() => {
            let newSecond = second - 1
            setSecond(newSecond)
            setCountdown(moment.duration(newSecond, 'seconds').asSeconds().toString() + "s")
            if (newSecond < 0) {
                clearInterval(timer);
                setCountdown("TIME UP!");
            }
        }, 1000);

        return () => {
            clearInterval(timer);
        }
    }, [second])



    return (
        <div id="error-page">
            <h1>Oops!</h1>
            <p>Sorry, an unexpected error has occurred.</p>
            <p>You'll be redirected to the previous page in {countdown.toString()}!</p>
            <p>
                <i>{error.statusText || error.message}</i>
            </p>
        </div>
    );
}