import React, { useEffect, useState } from "react";
import Card from "../Components/Layout/Card"
import "../Components/Layout/css/Card.css";
import { ReactComponent as MetodoNinja } from "../Components/Images/metodoNinja.svg"
import { ReactComponent as RoletaAoVivo } from "../Components/Images/roletaAoVivo.svg"
import paletasPurple from "../Components/Colors/purples"
import paletasBlueWhite from "../Components/Colors/blue-white"
import { Link } from "react-router-dom";


export default function Home(props) {
    const [pageTitle, setPageTitle] = useState('Company BEJ');
    useEffect(() => {
        document.title = pageTitle;

    }, [pageTitle]);

    const newTitle = (value) => {
        // console.log(value)
        setPageTitle(value);
    };
    return (
        <div className="Cards">
            <Card titulo="Entre para o grupo GRATUITO" color={paletasBlueWhite.color[2].rgb} bgColor={paletasBlueWhite.color[2].rgb} to={"/ninjabet"}>
                <Link to={"https://tradermillioniii.com"}>
                    <div className="RoletaMilionaria" >
                        <RoletaAoVivo />
                    </div>
                </Link>
            </Card>
            <Card titulo="Conheça a plataforma da Ninja.Bet" color={paletasPurple.color[0].rgb} bgColor={paletasPurple.color[0].rgb} to={"/ninjabet"}>
                <div className="MetodoNinja" >
                    <MetodoNinja />
                </div>
            </Card>

            {/* <Card titulo="Produto #3" color="#025959" bgColor="#025959" to={"/privacy-policy"}>
                <p>Conteúdo da página</p>
            </Card>
            <Card titulo="Produto #4" color="#012E40" bgColor="#012E40" to={"/privacy-policy"}>
                <p>Conteúdo da página</p>
            </Card>
            <Card titulo="Produto #5" color="#011526" bgColor="#011526" to={"/privacy-policy"}>
                <p>Conteúdo da página</p>
            </Card> */}
        </div>
    )
}